import React, { useState } from 'react';
import { connect } from 'react-redux';


import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';

import classes from './Login.module.scss'

import * as actions from '../../../store/actions/index';

const Login = (props) => {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [validated, setValidated] = useState(false);

    const onSubmit = () =>{
        props.onSubmit(email, password,true)
    }

    return(
        <Form className={classes.Login} noValidate validated={validated} onSubmit={onSubmit}>
            <Form.Group controlId="formBasicEmail">
                <Form.Label>Email address</Form.Label>
                <Form.Control 
                    value={email}
                    onChange={event => setEmail(event.target.value)}
                    type="email" 
                    placeholder="Enter email" />
            </Form.Group>

            <Form.Group controlId="formBasicPassword">
                <Form.Label>Password</Form.Label>
                <Form.Control
                    value={password}
                    onChange={event => setPassword(event.target.value)} 
                    type="password" 
                    placeholder="Password" />
            </Form.Group>
            <Button variant="primary" type="submit">
                Prijavi se!
            </Button>
            <Button variant="link" onClick={props.switchHandler}>
                Jo nema raun? Registriraj se!
            </Button>
        </Form>
    );
}

const mapStateToProps = state => {
    return{

    }
}


const mapDispatchToProps = dispatch => {
    return {
        onSubmit: (email, password, isLogin) => dispatch(actions.login(email, password, !isLogin))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Login);